export default {
	props : {
		list : {
			type : Array,
			default : () => ([])
		}
	},
	data(){
		return {
			curIndex : -1,
		}
	},
	methods:{
		next(){
			this.curIndex++;
		},
		prev(){
			this.curIndex--;
		}
	}
}
