import UiButton from '@/components/UiButton.vue'
export default {
	components: { UiButton },
	props:{
		tabKey : {
			type : String,
			default : 'detail'
		}
	},
	data(){
		return {}
	},
	methods:{
		handleJump(val){
			this.$emit('jump', val)
		},
		handleAddCart(){
			this.$emit('addCart')
		}
	}
}
